import React from 'react'
import images from '../../assets/logo.png'
import { Link } from 'react-router-dom'
export default function Navbar({ userdata, logout }) {
    return <>

        <nav className="navbar navbar-expand-lg navbar-light main-bg">
            <div className="container-fluid">
                <a className="navbar-brand text-white" href="#"><img src={images} alt="" className='Logoimages' />Game Over</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="fa-solid fa-bars main-color fa-xl"></span>
                    
                                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                {userdata !== null ?<>
                 <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link active" aria-current="page" to={''}>Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/All'}>All</Link>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link  dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Platforms
                            </Link>
                            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><Link className="dropdown-item"  to={`Platforms/pc`} >Pc</Link></li>
                                <li><Link className="dropdown-item" to={`Platforms/Browser`} >Browser</Link></li>
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                sort-by
                            </a>
                            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><Link className="dropdown-item" to={'sortby/ReleaseDate'}>release-date</Link></li>
                                <li><Link className="dropdown-item" to={'sortby/Popularity'}>popularity</Link></li>
                                <li><Link className="dropdown-item" to={'sortby/Alphabetical'}>alphabetical</Link></li>
                                <li><Link className="dropdown-item" to={'sortby/Relevance'}>relevance</Link></li>
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Categories
                            </a>
                            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><Link className="dropdown-item" to={'Category/Racing'}>racing</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Sports'}>sports</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Social'}>social</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Shooter'}>shooter</Link></li>
                                <li><Link className="dropdown-item" to={'Category/OpenWorld'}>open world</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Zombie'}>zombie</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Fantasy'}>fantasy</Link></li>
                                <li><Link className="dropdown-item" to={'Category/ActionRBG'}>action-rbg</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Action'}>action</Link></li>
                                <li><Link className="dropdown-item" to={'Category/Fight'}>fight</Link></li>
                                <li><hr className="dropdown-divider" /></li>
                                <li><Link className="dropdown-item" to={'Category/Battle'}>battle Royale</Link></li>
                            </ul>
                        </li>
                    </ul>
                    </>:''}
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                    {userdata == null ? <>
                        <li className="nav-item">
                            <Link className="nav-link" aria-current="page" to={'/Login'}>Login</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" aria-current="page" to={'/Register'}>Register</Link>
                        </li>
                       
                    </>: <li className="nav-item">
                            <a className="nav-link" aria-current="page"  onClick={logout} href="#">Log Out</a>
                        </li>}
                    </ul>

                </div>
            </div>
        </nav>
    </>
}
