import { useFormik } from 'formik'
import React, { useState } from 'react'
import * as Yup from 'yup'
import { Link, Navigate } from 'react-router-dom'
import './Register.css'
import axios from 'axios'
import { Helmet } from 'react-helmet'
export default function Register() {
  const [loading, setloading] = useState('')
  const [messgeEroor, setmessgeEroor] = useState('')
  let validate = Yup.object({
    name: Yup.string().required('name is required').min(3, 'name minlength is 3').max(16, 'name maxlength is 16'),
    email: Yup.string().required('Email is required').email('Invalid email address'),
    password: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with uppercase ....'),
    rePassword: Yup.string().required('repassword is required').oneOf([Yup.ref('password')], 'password and repassword doesn`t match....'),
    Phone: Yup.string().required('Phone is required').matches(/^01[0125][0-9]{0,8}$/, 'phone must be valid number...')
  })
  let Formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      password: '',
      rePassword: '',
      Phone: ''
    },
    onSubmit: getdata,
    validationSchema: validate
  })
  async function getdata(values) {
    setloading(true)
    let { data } = await axios.post(`https://route-ecommerce.onrender.com/api/v1/auth/signup`, values).catch((error) => {
      setloading(false)
      setmessgeEroor(error.response.data.message)
      console.log(error.response.data.message);
    })
    if (data.message === 'success') {
      console.log(data);
      setloading(false)
      Navigate('/Login')

    }

  }
  return <>
    <Helmet>
            <title>Register</title>
        </Helmet>
    <div className="wrapper mb-4">
      <div className="container main">
        <div className="row rows mt-3">
          <div className="col-md-6 right">
            <div className="Input-box">
              <header className='h2 '>Create account</header>
              {messgeEroor ? <div className='alert alert-danger'>{messgeEroor}</div> : ''}
              <form onSubmit={Formik.handleSubmit}>
                <div className="input-field">
                   <input type="text" onChange={Formik.handleChange} value={Formik.values.name} onBlur={Formik.handleBlur} className="input" name='name' id="name" required />
                  <label htmlFor="name">Name</label>
                  {Formik.errors.name && Formik.touched.name ? <div className='alert alert-danger'>{Formik.errors.name}</div> : ''}
                </div>
                <div className="input-field">
                <input type="text" onChange={Formik.handleChange} value={Formik.values.email} onBlur={Formik.handleBlur} className="input" name='email' id="email" required />
                  <label htmlFor="email">Email</label>
                  {Formik.errors.email && Formik.touched.email ? <div className='alert alert-danger'>{Formik.errors.email}</div> : ''}
</div>
                <div className="input-field">
                <input type="password" onChange={Formik.handleChange} value={Formik.values.password} onBlur={Formik.handleBlur} className="input" name='password' id="password" required />
                  <label htmlFor="password">password</label>
                  {Formik.errors.password && Formik.touched.password ? <div className='alert alert-danger'>{Formik.errors.password}</div> : ''}

                </div>
                <div className="input-field">
                  <input type="password" className="input" onChange={Formik.handleChange} value={Formik.values.rePassword} onBlur={Formik.handleBlur} name='rePassword' id="rePassword" required />
                  <label htmlFor="re-password">re-password</label>
                  {Formik.errors.rePassword && Formik.touched.rePassword ? <div className='alert alert-danger'>{Formik.errors.rePassword}</div> : ''}

                </div>

                <div className="input-field">
                <input type="Phone" className="input" onChange={Formik.handleChange} onBlur={Formik.handleBlur} value={Formik.values.Phone} id="Phone" name='Phone' required />
                  <label htmlFor="Phone">Phone</label>
                  {Formik.errors.Phone && Formik.touched.Phone ? <div className='alert alert-danger'>{Formik.errors.Phone}</div> : ''}

                </div>
                <div className="input-field">
                  {/* <button type='submit' className='submit mt-1'>Sign Up</button> */}
                  {loading ? <button type='button' className='submit main-color text-white'><i className='fas fa-spinner fa-spin'></i></button>
                    : <button disabled={!(Formik.isValid && Formik.dirty)} type='submit' className='submit mt-1'>Sign Up</button>
                  }
                </div>
                <div className="signin pt-3">
                  <span className='fw-bold'>Already have an account? <Link className='text-white' to={'../Login'}>Sign in here</Link></span>
                </div>
              </form>
            </div>
          </div>
          <div className="col-md-6 side-images">
            <div className='images'>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
}
