import { useFormik } from 'formik'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import './Login.css'
import axios from 'axios'
import { Helmet } from 'react-helmet'
export default function Login({ SaveUserData }) {
    let Navigate = useNavigate()

    let BaseUrl = `https://route-ecommerce.onrender.com`
    let [loading, Setloading] = useState(false)
    let [messgeEroor, SetmessgeEroor] = useState('')
    let validate = Yup.object({
        email: Yup.string().required('Email is required').email('Invalid email address'),
        password: Yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/, 'password must start with uppercase ....'),
    })
    let Formik = useFormik({
        initialValues: {
            email: '',
            password: ''

        },
        onSubmit: getdata,
        validationSchema: validate
    })
    async function getdata(values) {
        Setloading(true)
        let { data } = await axios.post(`${BaseUrl}/api/v1/auth/signin`, values).catch((error) => {
            Setloading(false)
            SetmessgeEroor(error.response.data.message)
            console.log(error.response.data.message);
            console.log(data);

        })
        if (data.message === 'success') {
            localStorage.setItem('useToken', data.token)
            Setloading(false)
            console.log(data);
            SaveUserData()
            Navigate('/')

        }
    }
    return <>
        <Helmet>
            <title>Login</title>
        </Helmet>
        <div className="wrapper">
            <div className="container main">
                <div className="row rowes mt-3">
                    <div className="col-md-6 side-image">
                        <div className='img'>
                            {/* <div className='text'>
                                <h2 className='text-white'>Hello Friedns !</h2>
                                <p>Enter your personal details and start journey with us</p>
                                <Link to="../Register" className=''>
                                    <button className='btn submit'>Sing Up</button></Link>
                            </div> */}
                        </div>
                    </div>
                    <div className="col-md-6 right">
                        <div className="input-box">
                            <header className='h1'>Sign in</header>
                            {messgeEroor ? <div className='alert alert-danger'>{messgeEroor}</div> : ''}
                            <form onSubmit={Formik.handleSubmit}>
                                <div className="input-field">
                                    <input type="text" className="input" onChange={Formik.handleChange} onBlur={Formik.handleBlur} values={Formik.values.email} name='email' id="email" required />
                                    <label htmlFor="email">Email</label>
                                    {Formik.errors.email && Formik.touched.email ? <div className='alert alert-danger'>{Formik.errors.email}</div> : ''}
                                </div>
                                <div className="input-field">
                                    <input type="password" className="input" onChange={Formik.handleChange} onBlur={Formik.handleBlur} values={Formik.values.password} id="password" name='password' required />
                                    <label htmlFor="password">Password</label>
                                    {Formik.errors.password && Formik.touched.password ? <div className='alert alert-danger'>{Formik.errors.password}</div> : ''}
                                </div>
                                {/* <div className="signin">
                                    <Link to={'../forgetpassword'}> <span className='main-color fw-bolder'>Forget your password?</span></Link>
                                </div> */}
                                <div className="input-field">
                                    {loading ? <button type='button' className='submit main-color  text-white'><i className='fas fa-spinner fa-spin'></i></button>
                                        : <button disabled={!(Formik.isValid && Formik.dirty)} type='submit' className='submit mt-1'>Sign In</button>
                                    }
                                    {/* <input type="submit" className="submit mb-3" value="" /> */}
                                </div>
                                <div className="signin pt-3">
                                    <span className=' fw-bold'> Create new account? <Link className='text-white' to='../Register'>Sing Up here</Link></span>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
}
