import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom';
import axios from 'axios';
import './GamesDetails.scss'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import Loading from '../Loading/Loading';
import { Helmet } from 'react-helmet';
export default function GameDetails() {
    let { id } = useParams()
    console.log(id);
    const [Details, setDetails] = useState()
    const [Screenshots, setScreenshots] = useState()
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        addprodect()
    }, [])
    async function addprodect() {
        setLoading(true)
        const options = {
            method: 'GET',
            url: 'https://free-to-play-games-database.p.rapidapi.com/api/game',
            params: { id: id },
            headers: {
                'X-RapidAPI-Key': 'f2b536615emsh5645e15d92ae651p17c4f5jsn5a9235808f44',
                'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
            }
        };

        try {
            const response = await axios.request(options);
            console.log(response.data);
            setDetails(response.data)
            setScreenshots(response.data.screenshots)
            console.log(response.data.screenshots);
            setLoading(false)
        } catch (error) {
            console.error(error);
        }
    }


    return <>
     <Helmet>
      <title>GameDetails</title>
    </Helmet>
          {loading == true ? <Loading /> : null}
        <div className='GameDetails' style={{ backgroundImage: `url(${Details?.thumbnail})` }}>
            <div className='layer'>
            <div className='container'>
                <div className='row pt-3'>
                    <div className='col-lg-4'>
                        <div className='Image'>
                            <img src={Details?.thumbnail} className='w-100' alt="" />
                            <div className='row'>
                                <div className='col-lg-3'>
                                    <button className="btn2 btn mt-3 w-100">FREE</button>

                                </div>
                                <div className='col-lg-9'>
                                    <Link to={Details?.game_url}>
                                        <button className='mt-3  w-100 btn btn-primary'>PLAY NOW</button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='col-lg-7'>
                        <h2>{Details?.title}</h2>
                        <p>{Details?.description}</p>
                        {Details?.minimum_system_requirements?.os ? <>
                            <h4>Minimum System Requirements</h4>
                            <p><span className='fw-bold'>graphics :</span> {Details?.minimum_system_requirements.graphics}</p>
                            <p><span className='fw-bold'>memory :</span> {Details?.minimum_system_requirements.memory}</p>
                            <p><span className='fw-bold'>os :</span> {Details?.minimum_system_requirements.os}</p>
                            <p><span className='fw-bold'>processor :</span> {Details?.minimum_system_requirements.processor}</p>
                            <p><span className='fw-bold'>storage :</span> {Details?.minimum_system_requirements.storage}</p>
                        </> : ''}

                        <h4>{Details?.title} Screenshots</h4>
                        <OwlCarousel className='owl-theme' autoplay items={1} >
                            {Screenshots?.map((element) => {
                                return <div className='iamges' key={element.id}>
                                    <img src={element.image} className='w-100' alt="" />
                                </div>

                            })

                            }          </OwlCarousel>
                    </div>
                </div></div>
            </div></div>
    </>
}
