import axios from 'axios';
import './All.scss'
import { Link } from 'react-router-dom';
import Loading from '../Loading/Loading';
import Hooks from '../../Hooks/Hooks';
import { Helmet } from 'react-helmet';

export default function All() {
  let { allData, loading } = Hooks(
    "https://free-to-play-games-database.p.rapidapi.com/api/games"
  );
  return <>
   <Helmet>
      <title>All</title>
    </Helmet>
      {loading == true ? <Loading /> : null}
    <div className='container'>
      <div className='row g-3'>
        {allData.map((items, index) => {
          return <div key={index} id={items.id} className='col-lg-3 col-md-6'>
            <Link className='text-decoration-none main-color' to={`/gameDetails/555`}>
              <div className="card mt-3">
                <img src={items.thumbnail} className="card-img-top" alt="..." />
                <div className="card-body ">
                  <div className='row'>
                    <div className='col-lg-9'>
                      <h4 className="card-text overflow">{items.title}</h4>
                      <p className='card-text overflow'>{items.short_description}</p>
                    </div>
                    <div className='col-lg-3 text-end'>
                      <span className="badge badge-ftg py-2 px-2 ">FREE</span>
                    </div>
                  </div>
                  <p className='badge platform px-2 my-2 rounded-3 '>{items.platform}</p>

                </div>
              </div>
            </Link>
          </div>
        })}
      </div>
    </div>


  </>
}
